input[type='checkbox'] {
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  width: 20px;
  height: 20px;
  border: 2px solid #ccc;
  border-radius: 4px;
  outline: none;
  cursor: pointer;
}

input[type='checkbox']:checked {
  background-color: #007bff;
  border-color: #007bff;
}

input[type='checkbox'] + label {
  margin-left: 5px;
  font-size: 16px;
  color: #333;
}
